.reportPage {
    max-width: 100%;
    padding: 20px;
    background: linear-gradient(180deg, rgba(0, 0, 124, 1) 10%, rgba(40, 40, 80, 1) 50%, rgba(20, 20, 40, 1) 90%);
    color: white;
    border-radius: 10px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.head {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding: 10px;
    border-bottom: 2px solid white;
}

.head h1 {
    font-size: 24px;
    font-weight: bold;
}

.head h3 {
    font-size: 18px;
}

.period {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin: 20px 0;
}

.period button {
    color: white;
}

.reportTable {
    width: 100%;
    margin-top: 20px;
    background-color: transparent;
}

.reportPage h1 {
    font-size: 20px;
    margin-top: 30px;
}

.reportPage table {
    width: 100%;
    margin-top: 20px;
    border-collapse: collapse;
}

.reportPage th, .reportPage td {
    padding: 10px;
    text-align: left;
}

.reportPage th {
    background-color: #444;
}

.reportPage td {
    background-color: #333;
}

.reportPage .searchButton {
    color: white;
    border-color: white;
}
