.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(270deg, rgba(248,249,250,1) 15%, rgba(0,0,124,1) 40%, rgba(0,0,124,1) 60%, rgba(248,249,250,1) 85%);
    padding: 15px 20px;
    position: sticky;
    top: 0;
    width: 99%;
    z-index: 1000;
    height: 80px;
}
.setting {
    position: fixed;
    color: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #2a2a2a;
    padding: 20px;
    border-radius: 10px;
    z-index: 1000;
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
    animation: fadeIn 0.3s ease-in-out;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    width: 600px;
    max-height: 90vh;
    overflow-y: auto;
    padding-bottom: 50px;
}

/* Ліва секція (іконка + заголовок) */
.leftSection {
    display: flex;
    align-items: center;
    gap: 10px;
}

/* Головний блок з кнопками – знаходиться строго по центру екрану */
.navWrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

/* Вирівнювання кнопок */
.navButtons {
    display: flex;
    justify-content: center;
    gap: 15px;
}

/* Права секція (тільки іконка) */
.rightSection {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Іконки */
.icon {
    cursor: pointer;
    color: black;
    transition: transform 0.2s ease-in-out;
}

.icon:hover {
    transform: scale(1.1);
}

/* Заголовок */
.leftSection h3 {
    margin: 0;
    color: black;
    font-size: 20px;
    text-align: left;
}


.container a {
    text-decoration: none;
}

.container button {
    background: #007bff;
    color: #f0f0f0;
    font-weight: bold;
    transition: 0.3s;
    border-radius: 5px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
}


.container button:hover {
    background: #0056b3;
    color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
}

.activeLink button {
    background: darkblue !important;
    color: white !important;
    box-shadow: 0px 0px 12px rgba(0, 123, 255, 0.9) !important;
    transform: scale(1.05);
}
.list {
    padding: 5px;
}
.iconSet {
    cursor: pointer;
    color: white;
    transition: transform 0.2s ease-in-out;
}

.iconSet:hover {
    transform: scale(1.1);
}

.settingAdd {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #2a2a2a;
    padding: 20px;
    border-radius: 10px;
    z-index: 1000;
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
    animation: fadeIn 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 400px;
    max-height: 90vh;
    overflow-y: auto;
}