.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.overlay {
    position: relative;
    width: 100%;
    height: 100%;
}

.backgroundImage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.content {
    position: absolute;
    right: 10%;  /* Зміщує вміст праворуч */
    top: 20%;  /* Зміщує вміст вниз, починаючи з 30% висоти екрану */
    transform: translateY(-20%); /* Відцентровує блок по вертикалі */
    z-index: 1;
    color: white;
    text-align: left;
    width: 30%; /* Встановлюємо ширину для центрування в правій частині */
}

.heading {
    font-size: 3rem;
    margin-bottom: 30px;
}

.subheading {
    font-size: 1.5rem;
    margin-bottom: 30px;
}

.addButton {

    margin-bottom: 20px;
}

.storeList {
    margin-bottom: 20px;

}

.storeLink {
    display: block;
    color: white;
    text-decoration: none;
    margin-bottom: 10px;
    padding: 5px;
    width: max-content;

}

.addStoreForm {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #2a2a2a;
    padding: 20px;
    border-radius: 10px;
    z-index: 1000;
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
    animation: fadeIn 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 400px;
    max-height: 90vh;
    overflow-y: auto;
}

.inputField {
    margin-bottom: 15px;
    width: 100%;
}

.buttons {
    display: flex;
    justify-content: space-between;
}

.createButton {
    background-color: #4CAF50;
    color: white;
}

.cancelButton {
    background-color: #f44336;
    color: white;
}
