.salesPage {
    max-width: 100%;
    padding: 20px;
    background: linear-gradient(180deg, rgba(0,0,124,1) 10%, rgba(40,40,80,1) 50%, rgba(20,20,40,1) 90%);
    color: white;
    border-radius: 10px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
}

.head {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 100%;
    padding: 10px;
    border-bottom: 2px solid white;
}

.head h1 {
    font-size: 24px;
    font-weight: bold;
}

.head h3 {
    font-size: 18px;
}

.period {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin: 20px 0;
}
.period button {
    color: white;
    border-color: white;
}
.salesTable {
    width: 100%;
    margin-top: 20px;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.salesAddPage {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #2a2a2a;
    padding: 20px;
    border-radius: 10px;
    z-index: 1000;
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
    animation: fadeIn 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 400px;
    max-height: 90vh;
    overflow-y: auto;

}
.customerAddPage {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #2a2a2a;
    padding: 20px;
    border-radius: 10px;
    z-index: 1000;
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
    animation: fadeIn 0.3s ease-in-out;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    width: 600px;
    max-height: 90vh;
    overflow-y: auto;
    padding-bottom: 50px;
}
.addHead {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.addHead .MuiInputLabel-root,
.addHead .MuiOutlinedInput-root {
    color: white;
    border-color: white;
}

.addHead .MuiOutlinedInput-notchedOutline {
    border-color: white;
}

.addHead .MuiSelect-icon {
    color: white;
}



.salesAddPage button {

    color: white;
}

.salesAddPage button:hover {
    background-color: #004d40;
}

.closeButton {
    background-color: #d32f2f  !important;
}

.closeButton:hover {
    background-color: #b71c1c;
}
