
.container {
    max-width: с;
    padding: 20px;
    background: linear-gradient(180deg, rgba(0,0,124,1) 10%, rgba(40,40,80,1) 50%, rgba(20,20,40,1) 90%);
    color: white;
    border-radius: 10px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
}

h1 {
    text-align: center;
    font-size: 28px;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}

.search-container {
    display: flex;
    justify-content: center;
    margin: 20px;
}



.table-container {
    margin-top: 20px;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(255, 255, 255, 0.1);
}

.total {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    padding: 15px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 0px 4px 8px rgba(255, 255, 255, 0.1);
    font-size: 18px;
    font-weight: bold;
}

.total div {
    display: flex;
    align-items: center;
    gap: 10px;
}

.total h3 {
    margin: 0;
    font-size: 18px;
}

.total span {
    font-size: 20px;
    font-weight: bold;
    color: #00d4ff;
}
button {
    background: #007bff;
    color: white;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

button:hover {
    background: #0056b3;
    transform: translateY(-2px);
    box-shadow: 0px 4px 8px rgba(0, 123, 255, 0.5);
}

